<template>
    <div>
        <el-card shadow="never" style="min-height: 600px;margin-bottom: 20px;">
            <el-collapse v-model="active" accordion>
                <el-collapse-item title="" name="deploy">
                    <el-carousel height="352px" :autoplay="false" @change="deployChange">
                        <el-carousel-item v-for="(item,index) in deploy.images" :key="'deploy'+index">
                            <div style="border-radius:5px; border: 1px solid #EBEEF5;height: 350px;">
                                <img :src="item" style="width: 100%;height:350px;">
                            </div>
                        </el-carousel-item>
                    </el-carousel>
                    <el-steps :active="deploy.active" align-center style="margin-top: 20px;" :process-status="deploy.status">
                        <el-step title="" description="https://github.com/GitHub-Laziji/GitHub-Laziji.github.io"></el-step>
                        <el-step title="" description=" /static/configuration.json  githubUsername  "></el-step>
                        <el-step title="" description="https://.github.io "></el-step>
                    </el-steps>
                </el-collapse-item>
                <el-collapse-item title="" name="use">
                    <el-carousel height="352px" :autoplay="false" @change="useChange">
                        <el-carousel-item v-for="(item,index) in use.images" :key="'use'+index">
                            <div style="border-radius:5px; border: 1px solid #EBEEF5;height: 350px;">
                                <img :src="item" style="width: 100%;height:350px;">
                            </div>
                        </el-carousel-item>
                    </el-carousel>
                    <el-steps :active="use.active" align-center style="margin-top: 20px;" :process-status="use.status">
                        <el-step title="Token" description="GitHubAPIToken"></el-step>
                        <el-step title="" description="Token"></el-step>
                        <el-step title="" description=""></el-step>
                    </el-steps>
                </el-collapse-item>
                <el-collapse-item title="" name="develop">
                    <div style="padding: 10px;line-height: 35px;color: #606266;font-size: 19px"> index.html  ./static/configuration.json,  npm run dev , ,  github ,  github-api  .github.io  /static/configuration.json</div>
                </el-collapse-item>
            </el-collapse>
        </el-card>
    </div>
</template>
<script>

    export default {
        data() {
            return {
                active: "deploy",
                deploy: {
                    images: [
                        require('../../assets/deploy0.png'),
                        require('../../assets/deploy1.png'),
                        require('../../assets/deploy2.png')
                    ],
                    status: "process",
                    active: 0,
                },
                use:{
                    images: [
                        require('../../assets/use0.png'),
                        require('../../assets/use1.png'),
                        require('../../assets/use2.png')
                    ],
                    status: "process",
                    active: 0,
                }
            }
        },
        mounted() {

        },
        methods: {
            deployChange(index) {
                this.deploy.status = index == 2 ? "success" : "process"
                this.deploy.active = index
            },
            useChange(index) {
                this.use.status = index == 2 ? "success" : "process"
                this.use.active = index
            }
        }
    }
</script>


